@import './variables';
@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@400;700&display=swap');
@import './button';
@import './adaptive';

* {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #16171B;
}

ul, li, dl {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  &:hover {
      text-decoration: none;
      color: inherit;
  }
}

p {
  margin: 0;
  padding: 0;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.App {
  margin: 0 auto;
  padding: 50px 15px;
  max-width: 1130px;

  .bg-decoration {
    display: block;
    margin: 20px auto 0;
  }
}

main {
  margin-top: 50px;
  position: relative;
}

.pulse {
  animation: pulse 1.5s ease-in-out .5s infinite
}

@keyframes pulse {
  0% {
      opacity: 1
  }
  50% {
      opacity: .4
  }
  100% {
      opacity: 1
  }
}

@media(max-width: 900px) {
  .App {
    max-width: 670px;
  }
}

@media(max-width: 670px) {

  .App {
    padding: 20px 15px;
  }

  main {
    margin-top: 20px;
  }

  .App .bg-decoration {
    max-width: 300px;
  }
}